.navbar_menu {
  background-color: #ffffff;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: auto;
  font-size: 2rem;
  margin-right: 46px;
  background: none;
  z-index: 1;
}
.contact_button{
  background-color: #f5f5f5;
}
.nav-menu {
  background-color: #446beb;
  width: 365px;
  height: 100vh;
  display: flex;
  z-index: 10;
  justify-content: center;
  position: fixed;
  border-radius: 0px 30px 30px 0px;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
  padding-left: 6rem;
}

.navbar-toggle-menu {
 
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
